
:root {
    --content-leave-transition-duration: 300ms;
    --content-enter-transition-duration: 500ms;
    --content-enter-transition-starttime: 350ms;
}

@font-face {
    font-family: "m5x7";
    src: url(/about-me/static/media/m5x7.1c36fac5.ttf),
         url(/about-me/static/media/m5x7.b99dd26c.woff),
         url(/about-me/static/media/m5x7.1681d9b9.woff2),
         url(/about-me/static/media/m5x7.dc97bf00.svg#filename);
}

@font-face {
    font-family: "m6x11";
    src: url(/about-me/static/media/m6x11.cc8dfaa3.ttf),
         url(/about-me/static/media/m6x11.06395abf.woff),
         url(/about-me/static/media/m6x11.4b67f4ca.woff2),
         url(/about-me/static/media/m6x11.ec0d4e88.svg#filename);
}

.header {
    font-family: "m5x7";
    font-size: 32px;
    font-weight: normal;
    font-style: normal;
}

.content {
    font-family: "m5x7";
    font-size: 32px;
    font-weight: normal;
    font-style: normal;
}

.content-enter {
    opacity: 0.01;
}

.content-enter.content-enter-active {
    opacity: 1;
    -webkit-transition-delay: 350ms;
            transition-delay: 350ms;
    -webkit-transition-delay: var(--content-enter-transition-starttime);
            transition-delay: var(--content-enter-transition-starttime);
    -webkit-transition: opacity 500ms ease-in;
    transition: opacity 500ms ease-in;
    -webkit-transition: opacity var(--content-enter-transition-duration) ease-in;
    transition: opacity var(--content-enter-transition-duration) ease-in;
}

.content-leave {
    opacity: 1;
}

.content-leave.content-leave-active {
    opacity: 0.01;
    -webkit-transition: opacity 300ms ease-in;
    transition: opacity 300ms ease-in;
    -webkit-transition: opacity var(--content-leave-transition-duration) ease-in;
    transition: opacity var(--content-leave-transition-duration) ease-in;
}

